export interface ICell {
    id: number // id > 0 - id , id: 0 - 
    shot: boolean //       
    //   -  
    //      -  - 
    //     -  - 
    //     -  -    
}

export type FieldType = ICell[][]

export enum StatusEnum {
    alive = "alive",
    injury = "injury",
    killed = "killed"
}

export enum PlacingTypeEnum {
    notSelect = "notSelect",
    manually = "manually",
    randomly = "randomly"
}


export interface IShip {
    id: number // id = 1...10
    size: number // , 1...4
    status: StatusEnum // , , 
    hitCount: number // 0...size -  ,    
    xIndex: number // 0...9 - 
    zIndex: number // 0...9
    rotateAngle: number // 0...3 - 
}

export interface IShips {
    [key: number]: IShip
}

export interface ICoord {
    xIndex: number
    zIndex: number
}

// 
// 1 -            (ceil.shot = false)
// 2 -  ceil.id = 0 - , ceil.shot = true
// 3 -  ceil.id != 0, ship(id).hitCount += 1,     
// 5 -  ship(id).hitCount != ship(id).size ---> ship(id).status = "injury"
// 4 -  ship(id).hitCount = ship(id).size ---> ship(id).status = "killed" -  

export enum MoverEnum {
    user = "user",
    pc = "pc"
}

export enum MoveStatusEnum {
    miss = "miss",
    injury = "injury",
    killed = "killed"
}

export interface IMoveListItem {
    mover: MoverEnum
    coord: ICoord
    status: MoveStatusEnum
}